@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Noto Sans JP", sans-serif;
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #0984e3;
  color: white;
  overflow-x: hidden;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

body::-webkit-scrollbar {
  display: none;
}

.section-header {
  text-align: center;
  font-size: 3rem;
  letter-spacing: 2px;
}

.front-page {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.front-page .font-page-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateY(-50%);
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.font-page-content h1 {
  font-size: 4rem;
  margin-top: -90px;
}

.font-page-content h1 span {
  color: #2d3436;
}

.font-page-content h3 {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 2rem;
}

.font-page-content button {
  cursor: pointer;
}

.font-page-content button i {
  margin-left: 5px;
}

/* Button styling */
.btn {
  border: none;
  padding: 10px 20px;
  margin: 10px;
}

.btn:focus {
  outline: none;
}

.btn-primary {
  border: 3px solid #2d3436;
  background: none;
  color: white;
  font-size: 1rem;
  transition: 0.3s;
}

.btn-primary:hover {
  background-color: #2d3436;
}

/*  End of Button styling */
/* Navbar Styling */
.navbar {
  width: 100%;
  height: 55px;
  bottom: 0;
  left: 0;
  background-color: #171715;
  border-bottom: 3px solid #00cec9;
  position: absolute;
  z-index: 500;
}

.navbar ul {
  display: flex;
}

.navbar ul li a {
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.3s;
  cursor: pointer;
  cursor-hover-color: #00cec9;
}

.nav-item {
  padding: 15px;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
  transition: all 0.3s ease;
}

.active {
  color: #00cec9 !important;
}

.social-links {
  position: absolute;
  z-index: 100;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
}

.social-links a {
  color: white;
}

.social-links a i {
  font-size: 2.1rem;
  margin-right: 20px;
}

/* End of Navbar Styling */
/* About section */
.about-section {
  width: 100%;
  height: 800px;
  padding: 3.4rem;
  background-color: #171715;
}

.about-section hr {
  width: 100px;
  margin: 0 auto;
  margin-top: 10px;
  border: 2px solid #2d3436;
}

.profile {
  display: flex;
  justify-content: center;
  margin-top: 100px;
}

.profile-description {
  width: 550px;
  margin-top: 60px;
}

.profile-description p {
  font-size: 1rem;
  margin-top: 10px;
}

.tools {
  margin-top: 45px;
  margin-left: 100px;
}

/* End of About section */
/* Project section */
.project-section {
  width: 100%;
  padding: 3rem;
  background-color: #2d3436;
}

.project-section h1 {
  margin-bottom: 30px;
}

.project-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.overlay {
  background-color: black;
  opacity: 0.7;
}

/* End of Project section */
/* Contact Section */
.contact {
  width: 100%;
  padding: 5rem;
  position: relative;
  background-color: #171715;
}

.contact-form {
  width: 600px;
  height: 600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #2d3436;
  border-radius: 20px;
  box-shadow: 0px 0px 10px black;
}

.contact-form h1 {
  margin-bottom: 30px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.contact-form input {
  width: 75%;
  padding: 15px;
  margin-bottom: 15px;
  border: none;
  border-radius: 10px;
}

.contact-form input:nth-child(4) {
  height: 100px;
}

.contact-form input:focus {
  outline: none;
}

/* End of Contact Section */

.project {
   width: 800px;
   height: 450px;
   position: relative;
   overflow: hidden;
   margin: 5px;
   box-shadow: 0px 0px 5px black;
}

.project img {
   width: 100%;
}

.project .overlay {
   z-index: 10;
   width: 100%;
   bottom: 0;
   height: 100%;
   position: absolute;
}

.overlay:hover {
   opacity: 0.7;
}

.project-info {
   width: 100%;
   height: 100%;
   top: 30%;
   padding: 2rem;
   position: absolute;
   z-index: 20;
   color: white;
   text-align: center;
}

.project-overlay {
   opacity: 0;
   transition: 0.4s ease;
}

.project-overlay:hover {
   opacity: 1;
}

.project-title {
   text-transform: uppercase;
   letter-spacing: 2px;
   font-size: 3rem;
}

.project-description {
   width: 600px;
   font-size: 1.5rem;
   margin: 0 auto;
}

.language-figure {
   bottom: 0;
   position: absolute;
   z-index: 10;
   background-color: var(--secondary-color);
   left: 0;
   padding: 5px 10px;
   border-radius: 10px 10px 0px 0px;
   color: white;
   margin-left: 30px;
}

.tool {
  width: 500px;
  height: 30px;
  position: relative;
  margin-bottom: 10px;
}

.tool .language {
  background-color: #8854d0;
  width: 150px;
  text-align: center;
  padding: 3px;
  z-index: 10;
  position: absolute;
  text-transform: uppercase;
}

.progress-bar,
.progress-bar-fill,
.language {
  width: 500px;
  height: 100%;
}

.progress-bar {
  position: absolute;
  top: 0;
  z-index: 5;
  text-align: right;
  background-color: #2d3436;
}

.progress-bar p {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  margin-right: 10px;
  z-index: 100;
}

.progress-bar-fill {
  border-radius: 20px;
  position: absolute;
  z-index: 7;
  width: 300px;
  background-color: #a55eea;
}

